// Default Button Style
.default_btn {
background: $primary_color;
color: #ffffff;
padding: 24px 41px;
position: relative;
@include border_radius(0);
border: none;
font-weight: 500;
font-size: 16px;
overflow: hidden;
z-index: 1;
&:after,
&:before {
  content: '';
  background: $heading_color;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  top: 0;
  transform-origin: left;
  z-index: -1;
  @include transform(rotate(90deg));
  @include transition(.3s);
  opacity: 0;
  visibility: hidden;
}
&:after {
  transform-origin: right;
  @include transform(rotate(-90deg));
}
&:hover {
  color: #ffffff;
  background: $primary_color;
  &:after,
  &:before {
    @include transform(rotate(0deg));
    visibility: visible;
    opacity: 1;
  }
}
}
.clear_both {
clear: both;
}

/* Hero Section Title */
.hero_section_title {
position: relative;
z-index: 5;
h4 {
  font-size: 14px;
  color: $primary_color;
  letter-spacing: 2px;
  font-weight: 400;
  text-transform: uppercase;
  position: relative;
  margin-bottom: 10px;
  padding-top: 28px;
  &:before {
    content: '';
    background: $primary_color;
    width: 10px;
    height: 10px;
    position: absolute;
    left: 50%;
    margin-left: -5px;
    top: 0;
  }
}
h1 {
  font-weight: 500;
  font-size: 40px;
  @media #{$mobile_device_xs2, $mobile_device_xs} {
    font-size: 30px;
  }
}
}

/* Default shape position */
.map_shape_2, .map_shape {
position: absolute;
left: 0;
top: 0;
z-index: -1;
@media #{$laptop_device_2} {
  width: 350px;
}
@media #{$ipad_device, $lg_mobile, $mobile_device} {
  width: 300px;
}
}
.map_shape_2 {
left: auto;
right: 0;
}

/* Triangle shape */
.triangle_shape, .triangle_shape_2 {
left: 120px;
position: absolute;
top: -80px;
opacity: .6;
@media #{$ipad_device, $lg_mobile, $mobile_device} {
  width: 350px;
}
@media #{$lg_mobile, $mobile_device} {
  left: 40px;
}
}
.triangle_shape_2 {
left: auto;
right: -120px;
}

/* Pagination */
.pagination {
margin: 0;
display: flex;
justify-content: center;
align-items: center;
@media #{$lg_mobile, $mobile_device, $mobile_device_xs2, $mobile_device_xs} {
  margin-top: 40px;
}
li {
  margin-right: 10px;
  a {
    width: 63px;
    height: 63px;
    text-align: center;
    line-height: 63px;
    padding: 0;
    @include border_radius(50% !important);
    background: #eaeaea;
    border: none;
    color: #ffffff;
    @include transition(.3s);
    font-size: 18px;
    font-weight: 500;
    &:hover {
      color: #ffffff;
      background: $primary_color;
      @include box_shadow(0 0 30px rgba(87, 184, 87, 0.3));
    }
  }
  &:last-child {
    margin-right: 0;
  }
}
li.active {
  a {
    background: $primary_color;
    color: #ffffff;
    @include box_shadow(0 0 30px rgba(87, 184, 87, 0.3));
    &:hover {
      background: $primary_color;
      color: #ffffff;
      @include box_shadow(0 0 30px rgba(87, 184, 87, 0.3));
    }
  }
}
}

/* =========================
#01 - PreLoader
============================*/
.spinner {
text-align: center;
font-size: 10px;
position: fixed;
left: 0;
top: 0;
background: $primary_color;
width: 100%;
height: 100%;
margin: 0;
z-index: 9999999999999;
display: flex;
align-items: center;
justify-content: center;
> div {
  background: #ffffff;
  height: 50px;
  width: 5px;
  display: inline-block;

  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
  margin: 0 2px;
}
.rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.rect3 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
.rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
}

/* =========================
#02 - Header
============================*/
.header_area {
.header_top {
  background: $heading_color;
  padding: 10px 0;
  @media #{$lg_mobile, $mobile_device} {
    text-align: center;
  }
  .header_left {
    p {
      color: $gray_color;
      font-weight: 400;
      font-size: 14px;
      a {
        color: #ffffff;
        display: inline-block;
        border-bottom: 1px solid #67696b;
        @include transition(.2s);
        line-height: 12px;
        &:hover {
          color: $primary_color;
        }
      }
      i {
        color: $primary_color;
        @include transform(rotate(-45deg));
        margin-right: 5px;
        font-size: 20px;
      }
      span {
        color: #ffffff;
        font-weight: 700;
        margin-left: 2px;
        display: inline-block;
      }
    }
  }
  @media #{$lg_mobile, $mobile_device} {
    .header_right {
      text-align: center;
    }
  }
}
.main_menu_area {
  @include box_shadow(0 0px 30px rgba(14, 26, 44, 0.05));
  width: 100%;
  background: #ffffff;
  z-index: 999999999;
  @media #{$ipad_device, $lg_mobile, $mobile_device} {
    padding: 15px 0;
  }
  @include transition(.3s);
  > .container {
    > .row {
      display: flex;
      align-items: center;
      @media #{$ipad_device, $lg_mobile, $mobile_device} {
        > * {
          flex: 1;
        }
      }
    }
  }
  .navbar-toggles {
    display: none;
    @media #{$ipad_device, $mobile_device, $lg_mobile, $mobile_device_xs2, $mobile_device_xs} {
      display: block;
    }
    right: 0;
    background: $heading_color;
    @include border_radius(0);
    margin: 0;
    span {
      background: #ffffff;
      height: 1px;
      margin-top: 6px;
      &:first-child {
        margin: 0;
      }
    }
  }
  .logo {
    display: inline-block;
    img {
      width: 70px;
    }
  }
  @media #{$small_desktop} {
    .navbar-collapse {
      padding-right: 0;
    }
  }
  .navbar-nav {
    float: right;
    @media #{$ipad_device, $lg_mobile, $mobile_device} {
      float: none;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
    li {
      position: relative;
      margin-left: 55px;
      @media #{$ipad_device, $lg_mobile, $mobile_device} {
        margin-left: 0;
        flex: 1;
        width: 100%;
        text-align: left;
        position: relative;
        border-bottom: 1px solid #000000;
        border-top: 1px solid #555556;
        &:first-child {
          border-top: none;
        }
        &:last-child {
          border-bottom: none;
        }
      }
      @media #{$laptop_device_2} {
        margin-left: 27px;
      }
      .highlight_dropdown {
        opacity: 0;
        position: absolute;
        padding: 60px;
        overflow: hidden;
        z-index: 99;
        top: 100%;
        background: $heading_color;
        width: 312px;
        text-align: left;
        left: -55px;
        transform-origin: top;
        transform: perspective(5em) rotateX(-90deg);
        @include box_shadow(0 0 30px rgba(47, 52, 55, 0.05));
        @include transition(.3s);
        @media #{$laptop_device_2} {
          left: -110px;
        }
        @media #{$ipad_device, $lg_mobile, $mobile_device} {
          left: 0;
          width: 100%;
          background: #000000;
          transform: perspective(0em) rotateX(0deg);
          opacity: 1;
          position: relative;
          display: none;
        }
        @media #{$large_desktop, $large_desktop, $small_desktop, $laptop_device_1, $laptop_device_2, $laptop_device_3} {
          display: block !important;
        }
        li {
          display: block;
          margin: 0;
          @media #{$ipad_device, $lg_mobile, $mobile_device} {
            border: none;
          }
          a {
            line-height: 20px;
            padding: 0;
            margin:0 0 30px 0;
            display: block;
            color: $gray_color;
            font-size: 16px;
            font-weight: 400;
            @include transition(.3s);
            &:hover {
            text-decoration: none;
              color: $primary_color;
            }
            @media #{$ipad_device, $lg_mobile, $mobile_device} {
              color: #ffffff;
            }
          }
          &:last-child {
            a {
              margin-bottom: 0;
            }
          }
        }
      }
      a {
        position: relative;
        color: $dark_gray;
        font-size: 18px;
        font-weight: 500;
        padding: 45px 0;
        @media #{$ipad_device, $lg_mobile, $mobile_device} {
          color: #ffffff;
          font-weight: 300;
        }
        @media #{$ipad_device, $lg_mobile, $mobile_device} {
          padding: 10px 0;
          display: block;
        }
        &:hover {
          background: none;
          color: $heading_color;
          @media #{$ipad_device, $lg_mobile, $mobile_device} {
            color: $primary_color;
          }
        }
      }
      &:first-child {
        margin-left: 0;
      }
      &:hover > .highlight_dropdown {
        transform: perspective(5em) rotateX(0deg);
        opacity: 1;
      }
      .menu_icon {
        display: none;
        cursor: pointer;
        position: absolute;
        right: 0;
        background: #000000;
        color: #ffffff;
        width: 40px;
        height: 100%;
        top: 0;
        text-align: center;
        line-height: 40px;
        @media #{$ipad_device, $lg_mobile, $mobile_device} {
          display: block;
        }
      }
    }
  }
  .close_menu_area_btn {
    display: none;
    color: #ffffff;
    position: absolute;
    top: 10px;
    font-size: 35px;
    font-weight: 100;
    text-align: center;
    left: 50%;
    cursor: pointer;
    @include transition(.3s);
    &:hover {
      color: $primary_color;
    }
  }
  @media #{$ipad_device, $lg_mobile, $mobile_device} {
    .collapse_responsive {
      position: fixed;
      left: -320px;
      background: $heading_color;
      width: 320px;
      text-align: left;
      z-index: 9;
      height: 100vh;
      top: 0;
      overflow-x: hidden;
      padding: 60px 10px 50px 10px;
      @include transition(.3s);
      .close_menu_area_btn {
        display: inline-block;
      }
      .collapse {
        display: block;
      }
    }
    .collapse_responsive.active {
      left: 0;
    }
    .responsive_button {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row-reverse;
    }
  }
}
}
.fixed-nav {
.header_area {
  .header_top {
    opacity: 0;
  }
  .main_menu_area {
    position: fixed;
    top: 0;
    left: 0;
