/* ===========================
#19 - Features
==============================*/
.feature_area {
.col-md-4 {
  &:last-child {
    .feature_box {
      margin-bottom: 0;
    }
  }
}
.feature_box {
  @include transition(.3s);
  @media #{$ipad_device} {
    margin-bottom: 30px;
  }
  @media #{$lg_mobile, $mobile_device} {
    width: 65%;
    margin: auto auto 30px auto;
  }
  @media #{$mobile_device_xs2, $mobile_device_xs} {
    width: 100%;
  }
  .feature_box_details {
    border: 2px solid #f2f2f2;
    border-bottom: none;
    padding: 35px 35px 35px 40px;
    @include transition(.3s);
    h3 {
      font-size: 20px;
      line-height: 26px;
      font-weight: 700;
      margin-bottom: 15px;
    }
    p {
      font-size: 16px;
      line-height: 30px;
      font-weight: 400;
      color: $dark_gray;
    }
  }
  .feature_img {
    position: relative;
    background: $primary_color url("../images/features/feature-1.jpg") center center no-repeat;
    background-size: cover;
    height: 195px;
    width: 100%;
  }
  .feature_img.bg_2 {
    background: $primary_color url("../images/features/feature-1.jpg") center center no-repeat;
    background-size: cover;
  }
  .feature_img.bg_3 {
    background: $primary_color url("../images/features/feature-1.jpg") center center no-repeat;
    background-size: cover;
  }
  a {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    letter-spacing: 2px;
    line-height: 20px;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    padding: 18px 40px;
    background: #f2f2f2;
    color: $dark_gray;
    &:hover {
      color: #ffffff;
    }
    &:after, &:before {
      background: $primary_color;
    }
  }
  &:hover {
    @include box_shadow(0 0 30px rgba(52, 55, 58, 0.10));
    .feature_box_details {
      border-left: 2px solid $primary_color;
      border-top: 2px solid $primary_color;
      border-right: 2px solid #ffffff;
    }
    a {
      color: #ffffff;
      &:after, &:before {
        opacity: 1;
        @include transform(rotate(0deg));
        visibility: visible;
      }
    }
  }
}
}
