/* =========================
#21 - Easy Step by Step
============================*/
.step_by_step_area {
padding-bottom: 152px;
@media #{$mobile_device_xs2, $mobile_device_xs} {
  padding-bottom: 150px;
}
position: relative;
.col-md-4 {
  &:last-child {
    .step_by_step_box {
      margin-bottom: 0;
    }
  }
}
.step_by_step_box {
  border: 2px solid #f2f2f2;
  padding: 60px 20px 50px 20px;
  position: relative;
  @media #{$ipad_device} {
    margin-bottom: 70px;
  }
  @media #{$lg_mobile} {
    width: 65%;
    margin: auto auto 70px auto;
  }
  @media #{$mobile_device, $mobile_device_xs, $mobile_device_xs2} {
    width: 100%;
    margin-bottom: 70px;
  }
  @include transition(.3s);
  > i {
    display: inline-block;
    background: linear-gradient(to bottom, #e2f8da, #ffffff);
    width: 125px;
    height: 125px;
    text-align: center;
    line-height: 125px;
    @include border_radius(50%);
    position: relative;
    font-size: 50px;
    z-index: 1;
    @include transition(.3s);
    color: $primary_color;
    margin-bottom: 30px;
    &:after {
      content: '';
      width: 75px;
      height: 75px;
      position: absolute;
      background: #ffffff;
      z-index: -1;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      border-radius: 50%;
      left: 50%;
      margin-left: -37.5px;
      top: -60px;
    }
  }
  h3 {
    font-size: 20px;
    font-weight: 700;
    line-height: 36px;
    margin-bottom: 28px;
  }
  ul {
    margin-bottom: 45px;
    li {
      color: $light_gray;
      font-size: 18px;
      line-height: 25px;
      font-weight: 300;
      margin-bottom: 10px;
    }
  }
  .btn_tx {
    font-size: 14px;
    letter-spacing: 2px;
    font-weight: 400;
    text-transform: uppercase;
    color: $heading_color;
    line-height: 15px;
    display: inline-block;
    text-decoration: none !important;
    @include transition(.3s);
  }
  .btn_icon {
    font-size: 24px;
    width: 64px;
    height: 64px;
    text-align: center;
    position: absolute;
    line-height: 64px;
    background: $primary_color;
    color: #ffffff;
    left: 50%;
    margin-left: -32px;
    bottom: -32px;
    opacity: 0;
    visibility: hidden;
    @include transform(scale(0.5));
    @include border_radius(50%);
  }
  &:hover {
    border-color: $primary_color;
    border-right: 2px solid #ffffff;
    border-bottom: 2px solid #ffffff;
    @include box_shadow(0 0 30px rgba(52, 55, 58, 0.1));
    > i {
      background: $primary_color;
      color: #ffffff;
      @include box_shadow(0 4px 10px rgba(124, 183, 95, 0.3));
    }
    .btn_tx {
      color: $primary_color;
    }
    .btn_icon {
      opacity: 1;
      visibility: visible;
      @include transform(scale(1));
    }
  }
}
}
.step_two_style {
position: relative;
z-index: 1;
overflow: hidden;
.step_by_step_shape, .step_by_step_shape_2 {
  z-index: -1;
}
.step_by_step_box {
