  &:before {
    content: '';
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-left: 2px solid $primary_color;
    border-top: 2px solid $primary_color;
    position: absolute;
    opacity: 0;
    @include transition(.3s);
  }
  &:hover {
    border: none;
    &:before {
      opacity: 1;
    }
    .step_btn {
      opacity: 1;
      @include transform(scale(1));
    }
  }
