  }
  .step_img_box.bg_3 {
    background: url("../images/step-1.jpg") center center no-repeat;
    background-size: cover;
  }
  .step_details {
    padding: 34px 40px 57px 40px;
    @media #{$ipad_device, $lg_mobile} {
      padding: 34px 42px 57px 42px;
    }
    span {
      display: block;
