    font-size: 24px;
    display: inline-block;
    line-height: 65px;
    color: #ffffff;
    background: $primary_color;
    @include border_radius(50%);
    text-align: center;
    position: absolute;
    left: 50%;
    margin-left: -32.5px;
    bottom: -32.5px;
    @include transform(scale(0.5));
    opacity: 0;
  }
}
}

