/*radio button*/
.custom_radio_btn {
li {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: $dark_gray;
  position: relative;
  margin-bottom: 26px;
  padding-left: 50px;
  cursor: pointer;
  &:before,
  &:after {
    content: '';
    background: #ffffff;
    position: absolute;
    left: 0;
    top: 50%;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    text-align: center;
    margin-top: -15px;
    border: 2px solid rgba(0, 0, 0, 0.12);
    @include transition(.3s);
  }
  &:after {
    content: "\f00c";
    font-family: fontawesome;
    background: $primary_color;
    border: none;
    color: #ffffff;
    @include transform(scale(0));
  }
}
li.active {
  color: $heading_color;
  &:after {
    @include transform(scale(1));
  }
}
}

