.header-wrapper--sticky {
position: absolute;
top: 0;
left: 0;
right: 0;
z-index: 6;
background: none;
background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0) 100%)
}
.header-wrapper--sticky .site-header:not(.site-header--stuck) {
background: none
}
.js-drawer-open--search .header-wrapper--sticky {
z-index: 28
}

.announcement {
position: relative;
z-index: 29;
transition: height 0.5s cubic-bezier(0, 0, 0.38, 1);
overflow: hidden;
background-color: #1c1d1d;
color: #fff
}
.announcement--opening {
height: 0
}
.announcement--opening .announcement__text {
opacity: 1
}
.announcement--opening .announcement__close {
opacity: 1
}
.announcement--closed {
transition: height 0.3s cubic-bezier(0, 0, 0.38, 1);
height: 0 !important
}
.announcement__text {
display: block;
padding: 5px 20px 4px;
font-size: 0.77778em;
opacity: 0;
transition: opacity 0.75s ease 0.5s
}
@media only screen and (min-width: 590px) {
.announcement__text {
    text-align: center;
    padding: 6px 20px 5px;
    font-size: 0.88889em
}
}
.announcement__text--open {
opacity: 1
}
.announcement__link {
display: block;
color: #fff
}
.announcement__link:hover {
color: #fff
}
.announcement__close {
display: block;
position: absolute;
top: 50%;
right: 0;
-webkit-transform: translateY(-50%);
transform: translateY(-50%);
padding: 0 10px;
z-index: 2;
opacity: 0;
transition: opacity 2s ease 0.8s;
cursor: pointer;
z-index: 2
}
.announcement__close .icon {
color: #fff;
width: 20px;
height: 20px;
vertical-align: middle
}
@media only screen and (min-width: 590px) {
.announcement__close .icon {
    width: 22px;
    height: 22px
}
}

